html {scroll-behavior: smooth;}
body{
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
    padding-top: 60px;
    line-height: 1.5;
    background-color: #fff;
}
h1,h2,h3,h4,h5,h6 {
    font-weight: 600;
    line-height: 1.5;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    color: #000;
}
h1, .h1 {font-size: 1.75em;}
h2, .h2 {font-size: 1.7em;}
h3, .h3 {font-size: 1.15em;}
h4, .h4 {font-size: 1.1em;}
h5, h6, .h5, .h6{font-size: 1em; }

p,li {color: #444; font-size: 1em;}
a {color: #444; text-decoration: none;  transition: 0.3s;}

#myBtn {
    display: none;
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 99;
    font-size: 18px;
    border: none;
    outline: none;
    background-color: #3face4;
    cursor: pointer;
  }
  .topbtn button:hover{
      background: #000;
      color: #fff;
  }

  .text-primary{
    color: #00356b!important;
  }


/*--------section header start---------*/

.nav-link{color: #fff!important; font-size: 0.8em; font-family: 'Montserrat', sans-serif; font-weight: 600;}
.bg-color{background: #00356b!important; }

#search select {font-size: 14px;}

@media screen and (max-width: 480px) {
    .home_nav{padding-left: 8px;}
}

/*--------section header end---------*/
.banner-tarea{
  position: absolute;
  top: 25%;
  right: 60%;
}

.banner-text{
  font-weight: 600;
  font-size: 36px;
  color: #fff;
  line-height: 128%;
  margin-bottom: 30px;
  margin-top: 60px;
  text-transform: lowercase;
  padding-bottom: 30px;
}

.main-banner-img{
  height: 100vh;
}

a.btn-banner {
  border: none;
  font-size: 1.2em;
  font-weight: 500;
  border-radius: 30px;
  letter-spacing: .20px;
  padding: 15px 42px;
  background-color: #fff;
  color: #00356b;
  text-decoration: none;
}

 a.btn-banner:hover {
  background: #bd5319;
  color: #fff;
} 



/*--------our centers area start---------*/

.text {
  font-size: 1.4em;
  line-height: 100%;
  font-weight: 700;
  color: #00356b;
}

.card:hover {
  -webkit-box-shadow: 10px 10px 31px -20px rgba(0,0,0,.5);
  -moz-box-shadow: 10px 10px 31px -20px rgba(0,0,0,.5);
  box-shadow: 10px 10px 31px -20px rgba(0,0,0,.5);
}

.card-img{
  /* background-image: url("assets/centers-img1.jpg"); */
  background-repeat: no-repeat;
  width: 100%;
  background-position: center;
  background-size: cover;
  height: 339px;
}

.find-our-centers .label {
  position: absolute;
  top: 3%;
  left: 5%;
  text-transform: uppercase;
  background-color: #f9fbff;
  opacity: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px;
  font-size: 11px;
}

.find-our-centers .name {
  font-size: 1.4em;
  line-height: 150%;
  font-weight: 600;
  padding: 0 0 20px;
}

.name a{
  text-decoration: none;
  color: #444;
}

.find-our-centers .card {
  max-width: 540px;
}

.sch-config-text{
  font-size: 15px;
  font-weight: 600;
}

.card-icon{
  font-size: 25px;
  color: #bd5319;
}

iframe{
  width: 100%;
  height: 500px;
}

.map{
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
}

.pagination {
  display: flex;
  justify-content: start;
  font-size: 12px;
}

.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
}

.pagination li.active a {
  background-color: #00356b;
  color: #fff;
}

.pagination a {
  border-radius: 5px;
}

.pagination li:hover:not(.active) {background-color: #ddd; border-radius: 5px;}
/* 
.filter-search{
  position: absolute;
  top: 10px;
  left: 10px;
} */
#update_map{
/* visibility: hidden; */
display: none;
}
/*--------footer-area start---------*/
.footer-area{
  background-color: #00356b;
} 

.icon-info i{
    font-size: 20px;
    color: #fff;
}

.footer-area a{ 
    color: #fff;
    text-decoration: none;
    font-size: 13px;
}

.footer-bottom p{ 
    color: #fff;
}

.footer-info p {
  font-size: 13px;
  color: #fff;
}

.footer-info li {
    padding-bottom: 10px;
    font-size: 13px;
    color: #fff;
}

.footer-widget h4.widget-title {
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 600;
}

.bottom-line {
  border-bottom: 1px solid #bfc4db;
}

/*--------footer-area end---------*/

@media screen and (max-width: 600px){

  .banner-tarea {
    position: absolute;
    top: 0%;
    right: 22%;
}

.banner-text {
  font-weight: 600;
  font-size: 18px;
  color: #fff;
  line-height: 128%;
  margin-bottom: 30px;
  margin-top: 60px;
  text-transform: lowercase;
  padding-bottom: 0px;
}

a.btn-banner {
  border: none;
  font-size: 0.8em;
  font-weight: 500;
  border-radius: 30px;
  letter-spacing: .20px;
  padding: 10px 17px;
  background-color: #fff;
  color: #00356b;
  text-decoration: none;
}

.main-banner-img{
  height: auto;
}

.footer-area{
  text-align: center;
} 
   
}